		background-color: #fff;
		color: #070716; } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_wh {
		color: #fff; } }

.subtitle {
	position: relative;
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	text-align: center;
	font-weight: 400;
	z-index: 2;
	&_wh {
		color: #fff; } }

.reasons {
	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }

	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px; }


	&_descr {
		text-align: justify;
		width: 343px; }

	&_title {
		line-height: 21px;

		font-size: 17px;
		font-weight: 400; }

	&_text {
		zcolor: #202020;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px; } }

.mobile {
	min-height: 864px;
	padding: 48px 0 80px;
	background: url('../img/center_bg.jpg') center (center / cover) no-repeat;

	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding: 92px 0 0 0;
		&_1 {
			background: url('../img/icon-1.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/icon-2.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/icon-3.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/icon-4.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/icon-5.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/icon-6.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-align: center;
			color: #ffffff;
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000;
			margin: 0 auto;
			border-radius: 100%;
			padding: 6px;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;

				transition: .3s all; }
			&::after {
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				content: '';
				display: block;
				width: 2px;
				height: 14px;

				transition: .3s all; }
			&:hover {
				&:before, &:after {} } } } }


.car {
	position: relative;
	min-height: 896px;
	padding: 56px 0 138px;
	background: url('../img/center_bg.jpg') center (center / cover) no-repeat;
	&_img {
		position: relative;
		z-index: 2;
		display: block;
		margin: 0 auto;
		margin-top: 56px; }
	&_text {
		margin-top: 4px;
		font-size: 17px;
		font-weight: 300;
		text-align: center;
		line-height: 24px;
		color: #222; } }


.black {
	position: absolute;
	left: 0;
	top: 0;
	min-height: 499px;
	width: 100%;
	background: #070716;
	z-index: 1;
	background-color: #000; }

.require {
	padding: 68px 0 93px;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		font-size: 22px;
		font-weight: 400;
		color: #1ca8c3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/plus_blue-1.svg') center (center / cover) no-repeat;
				vertical-align: middle; } } }

	&_descr {
		margin-top: 25px;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		color: #1ca8c3; }

	.warning {
		.require_title {}
		.require_list {
			li {
				&:before {
					background: url('../icons/plus_red-1.svg') center (center / cover) no-repeat; } } } } }

.img {
	.apple {
		display: inline-block;
		width: 90px; }
	.google {
		display: inline-block; } }

.map {
	min-height: 717px;
	padding: 49px 0;
	background: url('../img/footer_bg.jpg') center (center / cover) no-repeat;
